.app-header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #3173C4;
    padding: 10px 0; /* Ajusta o padding para reduzir o tamanho da barra */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1200px;
  }
  
  .app-logo {
    display: flex;
    height: 80px; /* Reduza o tamanho do logo para manter o alinhamento */
    margin-bottom: -40px;
    margin-left: -200px;
  }
  
  .region-buttons-container {
    display: flex;
    margin-top: -10px;
    margin-bottom: 50px;
    justify-content: center;
    align-items: center;    
  }
  
  .region-buttons {
    display: flex;
    gap: 10px;
  }
  
  .region-buttons button {
    background-color: #5A99F5;
    border: none;
    color: white;
    padding: 8px 12px;
    border-radius: 5px;
    font-size: 14px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .region-buttons button:hover {
    background-color: #3173C4;
  }
  
  .search-bar {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
  }
  
  .search-bar input {
    width: 250px;
    padding: 10px;
    font-size: 16px;
    border-radius: 25px;
    border: none;
    outline: none;
  }
  