.App {
  text-align: center;
  background: linear-gradient(to bottom, #3173C4, #5A99F5); /* Gradiente entre as cores */
  min-height: 100vh; /* Garante que o fundo cubra toda a tela */
  padding-top: 150px; /* Espaço para que o conteúdo não sobreponha o cabeçalho */
}

.pokemon-grid {
  display: grid;
  margin-top: 20px;
  margin-left: 200px;
  margin-right: 200px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
  justify-items: center;
}

.pokemon-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin: 10px;
  padding: 20px;
  background: #ffffff;
  border: 1px solid #dadada;
  border-radius: 10px;
  width: 150px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.pokemon-card img {
  max-width: 100px;
  height: auto;
}

.pokemon-card h3 {
  margin: 10px 0 0;
  font-size: 16px;
}

.pokemon-card img {
  width: 100px;
  height: 100px;  
  border-radius: 10px;
}

.pokemon-card img.grayscale {
  filter: grayscale(100%);
  opacity: 0.5;
}

.pokemon-card img:hover {
  transform: scale(1.1);
  transition: transform 0.2s;
}

/* Ícone Shiny */
.shiny-icon {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 20px !important;
  height: 20px !important;
  z-index: 10;
}

/* Contêiner principal da imagem do Pokémon */
.pokemon-image-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  margin: 0 auto;
}

/* Estilo para o contêiner da região */
.region-header {
  display: flex;
  
  align-items: center;
  justify-content: center;
  margin: 0px 0;
  position: relative;
}

/* Linha horizontal que acompanha o nome da região */
.region-header:before,
.region-header:after {
  content: "";
  flex: 1;  
  border-bottom: 8px solid #0f468a; /* Cor da linha */
  margin: 0 10px; /* Espaçamento entre o nome e as linhas */
}

/* Nome da região */
.region-title {
  background-color: #0f468a; /* Cor de fundo do título */
  padding: 5px 20px;
  color: white; /* Cor do texto */
  font-weight: bold;
  border-radius: 5px; /* Bordas arredondadas */
  text-align: center;
  font-size: 1.5em;
}

.app-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
}

.region-buttons-container {
  display: flex;
  justify-content: center;
  margin: 0px 0;
  gap: 15px;
}

.search-bar {
  display: flex;
  justify-content: flex-end;
  margin-left: auto;
}
